@import url(https://fonts.googleapis.com/css?family=Nunito|Shadows+Into+Light);
.card-container {
    height: 260px;
    width: 240px;
    border: 1px solid #888888;
    box-shadow: 1px 1px 8px #888888;
    margin: 15px;
    transition: -webkit-transform ease 1s;
    transition: transform ease 1s;
    transition: transform ease 1s, -webkit-transform ease 1s;
}

.clicked {
    border: 3px solid red;
}

.img-container {
    height: 50%;
    background-color: #E0DEDE;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.id-container {
    background-color: #C5C5C5;
    font-size: 10px;
    display: inline-block;
    padding: 3px 6px;
    position: absolute;
    bottom: 0;
    left: 0;
    color: #A52A2A;
}

.info-container {
    height: 50%;
    background-color: #FFF;
    display: flex;
    flex-direction: column;
    padding: 15px 0  0 20px;
}

.poke-img:hover {
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
}

/* .poke-img:hover{
    -webkit-transform: scale(1.5);
    -moz-transform: scale(1.5);
    -o-transform: scale(1.5);
    -ms-transform: scale(1.5);
    transform: scale(1.5);
} */

.poke-name {
    font-size: 20px;
    text-transform: capitalize;
    font-weight: bold;
    color: #000;
}

.class-info {
    font-size: 10px;
    text-transform: uppercase;
    color: #A52A2A;
    display: flex;
}

.class-info__item {
    border: 1px solid #888;
    border-radius: 5px;
    padding: 0 5px;
    margin-top: 5px;
}

.class-info__item:first-child {
    margin-right: 5px;
}

.poison {
    background-color: #8A2AE2;
    color: #FFF;
}

.grass {
    background-color: #008000;
    color: #FFF;
}

.fire {
    background-color: #FF8900;
    color: #000;
}

.flying {
    background-color: #8FB2C7;
    color: #000;
}

.water {
    background-color: #0000E1;
    color: #FFF;
}

.bug {
    background-color: #ADFF2F;
    color: #000;
}

.electric {
    background-color: #FFFF00;
    color: #000;
}

.evolution-info {
    margin-top: 10px;
    color: #888;
    font-size: 14px;
    width: 100px;
    border-left: 2px solid #888;
}

.evolution-info__text {
    display: block;
    padding-left: 5px;
}
.poke-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 30px 0;
}

.invalid-search {
    margin-top: 20px;
}
.input-label {
    position: absolute;
    bottom: 0;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 16px;
    font-family: 'Nunito', sans-serif;
    color: #888;
}

.input-label--hidden {
    display: none;
}

.input-form {
    display: flex;
    justify-content: center;
    position: relative;
}

.input {
    width: 250px;
    height: 50px;
    margin-top: 50px;
    border-radius: 0;
    border: 0;
    padding-left: 10px;
    font-size: 16px;
    font-family: 'Nunito', sans-serif;
    box-shadow: 1px 1px 8px #888;
    
}

@media all and (min-width: 768px) {
    .input {
        width: 500px;
        font-size: 20px;
        padding-left: 20px;
    }
    .input-label {
        font-size: 20px;
    }
}

@media all and (min-width: 1200px) {
    .input {
        width: 700px;
    }
}
.details-container {
    margin-top: 50px;
    line-height: 30px;
}

.pokemon-name {
    text-transform: capitalize;
    text-align: center;
}

.abilities-item {
    list-style: square;
    margin-left: 25px;
}

.pokeimg-container {
    display: flex;
    justify-content: center;
    border-bottom: 1px solid #000;
}

.pokeinfo {
    margin-top: 20px;
}

.evochain-info {
    display: inline-block;
    max-width: 280px;
    text-align: center;
}

.back-btn {
    display: inline-block;
    padding: 10px;
    background-color: #FF0000;
    color: #000;
    font-weight: 600;
    border-radius: 20px;
    margin-top: 20px;
}

@media all and (min-width: 1200px) {
    .pokeinfo-container {
        display: flex;
        margin-top: 20px;
    }

    .pokeimg-container {
        flex-direction: column;
        border-bottom: none;
        border-right: 1px solid #000;
    }

    .pokeinfo {
        margin-top: none;
        margin-left: 15px;
    }

    .evochain-info {
        max-width: none;
    }

    .back-btn {
        margin-top: 30px;
    }
}
.loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.loading-text {
    font-family: 'Shadows Into Light', cursive;
    font-size: 32px;
}

.top-corner{
    height: 100px;
    width: 100px;
    background-color: #000;
    position: fixed;
    top: 0;
    z-index: -1;
}
.top-corner__left {
    left: 0;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
}

.top-corner__right {
    right: 0;
    -webkit-transform: translate(50%, -50%) rotate(45deg);
            transform: translate(50%, -50%) rotate(45deg);
}

.bottom-corner{
    height: 200px;
    width: 200px;
    background-color: #FF0000;
    border-radius: 100px;
    position: fixed;
    bottom: 0;
    z-index: -1;
}
.bottom-corner__left {
    left: 0;
    -webkit-transform: translate(-50%, 50%);
            transform: translate(-50%, 50%);
}

.bottom-corner__right {
    right: 0;
    -webkit-transform: translate(50%, 50%);
            transform: translate(50%, 50%);
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    background-color: rgb(248, 248, 34);
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    width: 100%;
    font-family: 'Nunito', sans-serif;
    position: relative;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}
